<template>
<b-container v-if="categories.length >= 1" fluid="md" class="categories__container">
    <b-row  v-for="category in categories" :key="category" class="categories__row">
        <b-col class="categories__column">
            <b-card class="categories__card" :title="category.title">
                         <b-card style="margin-bottom: 10px;">
                             {{category.description}}
                         </b-card>
                               <b-button variant="dark" >Details</b-button>
            </b-card>
        </b-col>
    </b-row>
</b-container>
 <b-card v-else title="There's no categories">
        <b-label>Please add a category</b-label>
        <div style="padding-top 20px; display: flex; flex-direction: row; justify-content: flex-end;">
            <b-button variant="success" @click="generateRoute()">Add a Category</b-button>
        </div>
    </b-card>
</template>

<script>
import axios from 'axios'
import { mapGetters } from 'vuex'
export default {
    data() {
        return {
            categories: {}
         }
    },
       computed: {
             ...mapGetters({
                user: 'getUser'
             }),
    },
    methods: {
        generateRoute() {
            this.$router.push('/categories/add')
        }
    },
     async mounted() {
         const ctg = await axios.get('http://localhost:8000/api/categories').then(response => {
            this.categories = response.data
        })
    }
 }
</script>


